body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body {
  background-color: #edf2f4;
}

#root {
  height: 100vh;
  position: relative;
  text-align: center;
  font-family: 'Alice', serif;
  color: #001427;
}

.blocksDivText {
  text-align: center;
  padding: 10px 10px;
  font-size: 40px;
  font-weight: 700;
}

.blocksDiv {
  margin-top: 20px;
  height: 50px;
  display: flex;
  justify-content: center;
  transition: all 1s;
}

.blocks {
  color: #001427;
  background-color: #f5f2b8;
  border-radius: 5px;
  width: 150px;
  height: 100%;
  margin: 0px 10px;
  border: 1px solid #222;
  font-size: 25px;
  font-weight: 600;
  line-height: 50px;
  cursor: pointer;

  -webkit-box-shadow: 0px 2px 5px 1px rgba(34, 34, 34, 1);
  -moz-box-shadow: 0px 2px 5px 1px rgba(34, 34, 34, 1);
  box-shadow: 0px 2px 5px 1px rgba(34, 34, 34, 1);

  -webkit-animation-name: expand;
  -webkit-animation-duration: 1s;
  animation-name: expand;
  animation-duration: 1s;
}

.blocks:hover {
  font-size: 35px;
  line-height: 45px;
}

.blocks:active {
  -webkit-box-shadow: 0px 2px 5px 0px rgba(34, 34, 34, 1);
  -moz-box-shadow: 0px 2px 5px 0px rgba(34, 34, 34, 1);
  box-shadow: 0px 2px 5px 0px rgba(34, 34, 34, 1);
}

@keyframes expand {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes expand {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.btnDiv {
  display: flex;
  justify-content: space-between;
  width: 50vw;
  margin: auto;
}

.actionBtn {
  cursor: pointer;
  border: 1px solid #222;
  padding: 10px;
  width: 170px;
  line-height: 30px;
  margin: 30px 20px 5px;
  font-size: 20px;
  background-color: #fe9920;
  -webkit-box-shadow: 0px 2px 5px 1px rgba(34, 34, 34, 1);
  -moz-box-shadow: 0px 2px 5px 1px rgba(34, 34, 34, 1);
  box-shadow: 0px 2px 5px 1px rgba(34, 34, 34, 1);
}

.actionBtn:active {
  -webkit-box-shadow: 0px 2px 5px 0px rgba(34, 34, 34, 1);
  -moz-box-shadow: 0px 2px 5px 0px rgba(34, 34, 34, 1);
  box-shadow: 0px 2px 5px 0px rgba(34, 34, 34, 1);
}

.modeBtn {
  background-color: #f2f3d9;
  width: 70px;
  height: 36px;
  line-height: 20px;
  cursor: pointer;
  border: 1px solid #222;
  padding: 8px;
  margin: 30px 20px 5px;
  font-size: 18px;
  -webkit-box-shadow: 0px 2px 5px 1px rgba(34, 34, 34, 1);
  -moz-box-shadow: 0px 2px 5px 1px rgba(34, 34, 34, 1);
  box-shadow: 0px 2px 5px 1px rgba(34, 34, 34, 1);
  align-self: flex-end;
}

.modeBtnClicked {
  background-color: #fe9920;
}

.modeBtn:active {
  -webkit-box-shadow: 0px 2px 5px 0px rgba(34, 34, 34, 1);
  -moz-box-shadow: 0px 2px 5px 0px rgba(34, 34, 34, 1);
  box-shadow: 0px 2px 5px 0px rgba(34, 34, 34, 1);
}

.field {
  height: 49vh;
  width: 50vw;
  position: relative;
  margin: 5px auto 10px;
  transition: all 1s;
  border: 2px solid #222;
}

.svg {
  width: 300px;
  height: auto;
  fill: #42acac;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 30px;
  margin: auto;
}

#iris {
  fill: #000;
}

.eye {
  -webkit-animation-name: blink;
  -webkit-animation-duration: 4s;
  -webkit-animation-iteration-count: infinite;

  animation-name: blink;
  animation-duration: 4s;
  animation-iteration-count: infinite;
}

@keyframes blink {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 1;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes blink {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 1;
  }

  100% {
    opacity: 1;
  }
}

.speechBubble {
  width: 260px;
  border-radius: 50%;
  border: 4px solid #222;
  padding: 40px;
  text-align: center;
  font-size: 25px;
  font-family: 'Lakki Reddy', cursive;
  color: #001427;
  position: absolute;
  top: 20px;
  left: 40%;
  transform: translateX(-50%);
  background-color: #faf6f6;
}

.speechBubbleAnimated {
  -webkit-animation-name: pop;
  -webkit-animation-duration: 1s;
  animation-name: pop;
  animation-duration: 1s;
}

@keyframes pop {
  from {
    opacity: 0;
    top: 50px;
  }
  to {
    opacity: 1;
    top: 20px;
  }
}

@-webkit-keyframes pop {
  from {
    opacity: 0;
    top: 50px;
  }
  to {
    opacity: 1;
    top: 20px;
  }
}

.speechBubble:before {
  content: '';
  width: 0px;
  height: 0px;
  position: absolute;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #222;
  border-bottom: 10px solid transparent;
  right: 50%;
  bottom: -23px;
}

.speechBubble:after {
  content: '';
  width: 0px;
  height: 0px;
  position: absolute;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #faf6f6;
  border-bottom: 10px solid transparent;
  right: 50%;
  bottom: -18px;
}

.colorBar {
  display: flex;
  justify-content: center;
  margin: 20px auto;
  color: #001427;
  height: 70px;
  line-height: 85px;
  font-size: 30px;
  font-weight: 600;
}

.score {
  font-size: 25px;
  color: #001427;
}

.colorBarBlocks {
  margin: 10px;
  width: 100px;
  height: 100%;
  border: 1px solid #222;
  color: #fff;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
  font-size: 30px;
  font-weight: 600;
  line-height: 60px;
}

#colorBarBlock1 {
  background-color: #e50d2c;
}

#colorBarBlock2 {
  background-color: #7dc074;
}

#colorBarBlock3 {
  background-color: #58a3ce;
}

.title {
  position: fixed;
  bottom: 0;
  color: #72a98f;
  font-size: 4vw;
  padding: 0px 10px;
  font-family: 'Lakki Reddy', cursive;
}

/*MEDIA*/

@media screen and (max-width: 992px) {
  .field {
    height: 40vh;
    width: 100%;
    position: relative;
    transition: all 1s;
    border: 2px solid #222;
  }

  .blocksDivText {
    padding: 20px 10px;
    font-size: 20px;
    font-weight: 600;
  }

  .blocksDiv {
    margin-top: 10px;
    height: 40px;
    display: flex;
    justify-content: center;
    transition: all 1s;
  }

  .blocks {
    width: 110px;
    font-size: 20px;
    font-weight: 600;
    line-height: 40px;
  }

  .btnDiv {
    width: 100%;
  }

  .actionBtn {
    width: 120px;
    line-height: 30px;
    padding: 5px;
    font-size: 18px;
  }

  .blocks:hover {
    font-size: 20px;
  }

  .svg {
    width: 250px;
  }

  .speechBubble {
    width: 280px;
    padding: 40px;
  }

  .colorBar {
    margin: 10px auto;
    height: 70px;
    line-height: 75px;
    font-size: 20px;
    font-weight: 600;
  }

  .colorBarBlocks {
    width: 80px;
    margin: 5px;
    font-size: 25px;
  }
}

@media screen and (max-width: 576px) {
  .svg {
    width: 200px;
  }

  .speechBubble {
    width: 124px;
    padding: 12px;
    font-size: 20px;
    top: 10px;
    left: 30%;
    border: 2px solid #222;
  }

  .speechBubble:before {
    bottom: -20px;
  }

  .btnDiv {
    width: 100%;
  }

  .actionBtn {
    padding: 5px;
    margin: 30px 5px 5px;
    font-size: 18px;
  }

  .modeBtn {
    padding: 5px;
    margin: 30px 5px 5px;
    font-size: 16px;
    line-height: 25px;
  }
}

